import React from 'react';
import commonClasses from '../../Utils/CommonCss.module.css';
import 'antd/dist/antd.css';
import {  Button } from 'antd';


const buttonBar = props => (
    <div className={commonClasses.buttonBar}>
        <div className={commonClasses.description}>
            You are creating a configuration file for
            {props.useOs === 'windows' ? (
                <img
                    src={require('../../../assets/Icons/Windows.png')}
                    alt="Operating System "
                />
            ) : (
                <img
                    src={require('../../../assets/Icons/Linux.png')}
                    alt="Operating System "
                />
            )}
        </div>
        <div className={commonClasses.buttons}>
            <Button
                className={commonClasses.buttonBack}
                type="default"
                onClick={props.clickBack}
            >
                Back
            </Button>
            <Button
                className={commonClasses.buttonNext}
                type="primary"
                onClick={props.clickNext}
            >
                Next
            </Button>
        </div>
    </div>
);

export default buttonBar
