import React from 'react';
import commonClasses from '../../Utils/CommonCss.module.css';
import 'antd/dist/antd.css';
import { Button } from 'antd';

const buttonBar = props => (
    <div>
        <div className={props.styleName}>
            <div className={commonClasses.selectBarButtons}>
                <Button type="link" onClick={props.selectAll}>
                    Select All
                </Button>
                <Button type="link" onClick={props.unselectAll}>
                    Clear All
                </Button>
            </div>
        </div>
        {props.showErrorMsg ? (
            <div className={commonClasses.btnBarErrorMsg}>
                Please select at least one product.
            </div>
        ) : null}
    </div>
);

export default buttonBar;
